/* Style Variables */
:root {
	--white: #fefdf3;
	--black: #171615;
	--dark-blue: #082a4c;
	--med-blue: #0887E0;/*#09519b;*/
	--light-blue: #14a0cc;
	--bright-red: #eb3610;
}

/*
* nav bar diff color or top/bottom border, header on normal bg
* line dividers between items
* one main bright accent color for highlights
* understated sans-serif fonts
*/

/* Base Styles */

body {
	font-family: sans-serif;
	background-color: var(--white);
	color: var(--black);
	line-height: 1.4em;
	width: auto;
	margin: 0;
}

h2 {
	line-height: 1.2em;
}

a {
	padding: 1px 1px 0;
	text-decoration: none;
}

a:link {
	color: var(--black);
}

a:visited {
	color: var(--black);
}

a:focus {
	border: 2px solid var(--med-blue);
}

a:hover {
	border: 2px solid var(--med-blue);
}

.active {
	border-bottom: 3px solid var(--med-blue);
}

/* Header Styles */
header {
	font-size: 2.5em;
	padding-top: 0.5em;
	padding-bottom: 0.5em;
	padding-left: 5%;
	padding-right: 5%;
}

header a {
	font-weight: bold;
}

/* Navbar Styles */

nav {
	font-size: 1.1em;
	padding-left: 5%;
	padding-right: 5%;
	padding-top: 0.5em;
	padding-bottom: 0.5em;
	width: auto;
	border-top: 2px solid var(--black);
	border-bottom: 2px solid var(--black);
}

nav a {
	margin-right: 0.5em;
}

/* Main Block Styles */

main {
	max-width: 90%;
	margin: 5%;
	padding: 1em 2em 2em 2em;
	overflow: auto; /*to accomodate floating divs*/
	background-color: var(--white);
	border-radius: 3px;
	border: 0px solid var(--med-blue);
}

main hr {
	border: 2px solid #5D879E;
}

main h1 {
	text-align: center;
}

main a {
	padding: 1px 1px 0;
	text-decoration: none;
}

main a:link {
	color: var(--med-blue);
	border-bottom: 2px solid;
}

main a:visited {
	color: var(--med-blue);
}

main a:focus {
	border-bottom: 2px solid var(--med-blue);
	color: var(--med-blue);
}

main a:hover {
	border-bottom: 2px solid var(--med-blue);
	color: var(--med-blue);
}

/* Footer Styles */
footer {
	border-top: 2px solid var(--black);
	text-align: center;
	font-size: 0.7em;
	padding-top: 2em;
}